@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&family=Open+Sans:wght@400;600&display=swap);
:root { 
  --durham-green: #00734d;
  --durham-light-green: #00a868;
  --durham-gray: #e6e6e6;
  --white: #ffffff;
}

/* Add Google Fonts */

body {
  font-family: 'Open Sans', sans-serif;
  background-color: var(--durham-gray);
  color: var(--durham-green);
  padding-top: 60px; /* to avoid navbar overlap */
}

h1, h2, h3 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
}

h1 {
  font-size: 3rem;
  color: var(--durham-green);
}

h2 {
  font-size: 2.5rem;
  color: var(--durham-light-green);
}

h3 {
  font-size: 1.75rem;
  color: var(--durham-green);
}

p, ul, li {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.1rem;
  line-height: 1.6;
}

/* Navbar styling */
nav {
  background-color: var(--durham-green);
  padding: 15px 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}

nav li {
  margin: 0 20px;
}

nav a {
  color: var(--white);
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: background-color 0.3s;
}

nav a:hover {
  background-color: var(--durham-light-green);
  border-radius: 4px;
  padding: 5px 10px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 12px;
  margin: 12px 0;
  border: 1px solid var(--durham-light-green);
  border-radius: 8px;
  font-size: 1rem;
}

.contact-form button {
  padding: 12px 24px;
  background-color: var(--durham-green);
  color: var(--white);
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 600;
  transition: background-color 0.3s;
}

.contact-form button:hover {
  background-color: var(--durham-light-green);
}

.project-card {
  border: 1px solid var(--durham-light-green);
  padding: 20px;
  margin: 15px;
  background-color: var(--white);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  transition: transform 0.3s;
}

.project-card:hover {
  transform: translateY(-5px);
}

footer {
  text-align: center;
  padding: 20px;
  background-color: var(--durham-green);
  color: var(--white);
  font-size: 1.1rem;
  margin-top: 30px;
}

